/*--------------------------------------------------------------
# Normalize
--------------------------------------------------------------*/
html {
	font-family: sans-serif;
	-webkit-text-size-adjust: 100%;
	-ms-text-size-adjust: 100%;
}

body {
	margin: 0;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
main,
menu,
nav,
section,
summary {
	display: block;
}

audio,
canvas,
progress,
video {
	display: inline-block;
	vertical-align: baseline;
}

audio:not([controls]) {
	display: none;
	height: 0;
}

[hidden],
template {
	display: none;
}

a {
	background-color: transparent;
}

a:active,
a:hover {
	outline: 0;
}

abbr[title] {
	border-bottom: 1px dotted;
}

b,
strong {
	font-weight: bold;
}

dfn {
	font-style: italic;
}

h1 {
	font-size: 2em;
	margin: 0.67em 0;
}

mark {
	background: #ff0;
	color: #000;
}

small {
	font-size: 80%;
}

sub,
sup {
	font-size: 75%;
	line-height: 0;
	position: relative;
	vertical-align: baseline;
}

sup {
	top: -0.5em;
}

sub {
	bottom: -0.25em;
}

img {
	border: 0;
}

svg:not(:root) {
	overflow: hidden;
}

figure {
	margin: 1em 40px;
}

hr {
	box-sizing: content-box;
	height: 0;
}

pre {
	overflow: auto;
}

code,
kbd,
pre,
samp {
	font-family: monospace, monospace;
	font-size: 1em;
}

button,
input,
optgroup,
select,
textarea {
	color: inherit;
	font: inherit;
	margin: 0;
}

button {
	overflow: visible;
}

button,
select {
	text-transform: none;
}

button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
	-webkit-appearance: button;
	cursor: pointer;
}

button[disabled],
html input[disabled] {
	cursor: default;
}

button::-moz-focus-inner,
input::-moz-focus-inner {
	border: 0;
	padding: 0;
}

input {
	line-height: normal;
}

input[type="checkbox"],
input[type="radio"] {
	box-sizing: border-box;
	padding: 0;
}

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
	height: auto;
}

input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
	-webkit-appearance: none;
}

fieldset {
	border: 1px solid #c0c0c0;
	margin: 0 2px;
	padding: 0.35em 0.625em 0.75em;
}

legend {
	border: 0;
	padding: 0;
}

textarea {
	overflow: auto;
}

optgroup {
	font-weight: bold;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
}

td,
th {
	padding: 0;
}

/*--------------------------------------------------------------
# Typography
--------------------------------------------------------------*/
body,
button,
input,
select,
textarea {
	color: #404040;
	font-family: "PT Serif", Georgia, Cambria, "Times New Roman", Times, sans-serif;
	font-size: 16px;
	font-size: 1rem;
	line-height: 1.5;
}

h1, h2, h3, h4, h5, h6 {
	clear: both;
	font-family: "Source Sans Pro", "Lucida Grande", "Lucida Sans Unicode", "Lucida Sans", Geneva, Arial, sans-serif;
}

p {
	margin-bottom: 1.5em;
}

dfn, cite, em, i {
	font-style: italic;
}

blockquote {
	margin: 0 1.5em;
}

address {
	margin: 0 0 1.5em;
}

pre {
	background: #eee;
	font-family: "Courier 10 Pitch", Courier, monospace;
	font-size: 15px;
	font-size: 0.9375rem;
	line-height: 1.6;
	margin-bottom: 1.6em;
	max-width: 100%;
	overflow: auto;
	padding: 1.6em;
}

code, kbd, tt, var {
	font-family: Monaco, Consolas, "Andale Mono", "DejaVu Sans Mono", monospace;
	font-size: 15px;
	font-size: 0.9375rem;
}

abbr, acronym {
	border-bottom: 1px dotted #666;
	cursor: help;
}

mark, ins {
	background: #fff9c0;
	text-decoration: none;
}

big {
	font-size: 125%;
}

/*--------------------------------------------------------------
# Elements
--------------------------------------------------------------*/
html {
	box-sizing: border-box;
}

*,
*:before,
*:after {
	/* Inherit box-sizing to make it easier to change the property for components that leverage other behavior; see http://css-tricks.com/inheriting-box-sizing-probably-slightly-better-best-practice/ */
	box-sizing: inherit;
}

body {
	background: #fff;
	/* Fallback for when there is no custom background color defined. */
}

blockquote, q {
	quotes: "" "";
}

blockquote:before, blockquote:after, q:before, q:after {
	content: "";
}

hr {
	background-color: #ccc;
	border: 0;
	height: 1px;
	margin-bottom: 1.5em;
}

ul, ol {
	margin: 0;
	padding: 0;
}

ul {
	list-style: none;
}

ol {
	list-style: decimal;
}

li > ul,
li > ol {
	margin-bottom: 0;
	margin-left: 1em;
}

dt {
	font-weight: bold;
}

dd {
	margin: 0 1.5em 1.5em;
}

img {
	height: auto;
	/* Make sure images are scaled correctly. */
	max-width: 100%;
	/* Adhere to container width. */
}

figure {
	margin: 1em 0;
	/* Extra wide images within figure tags don't overflow the content area. */
}

table {
	margin: 1em 0;
	width: 100%;
}

/*--------------------------------------------------------------
# Forms
--------------------------------------------------------------*/
button,
input[type="button"],
input[type="reset"],
input[type="submit"] {
	border: 1px solid;
	border-color: #ccc #ccc #bbb;
	background: #e6e6e6;
	color: rgba(0, 0, 0, 0.8);
	font-size: 12px;
	font-size: 0.75rem;
	line-height: 1;
	padding: .6em 1em .4em;
}

button:hover,
input[type="button"]:hover,
input[type="reset"]:hover,
input[type="submit"]:hover {
	border-color: #ccc #bbb #aaa;
}

button:active, button:focus,
input[type="button"]:active,
input[type="button"]:focus,
input[type="reset"]:active,
input[type="reset"]:focus,
input[type="submit"]:active,
input[type="submit"]:focus {
	border-color: #aaa #bbb #bbb;
}

input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"],
input[type="number"],
input[type="tel"],
input[type="range"],
input[type="date"],
input[type="month"],
input[type="week"],
input[type="time"],
input[type="datetime"],
input[type="datetime-local"],
input[type="color"],
textarea {
	color: #666;
	border: 1px solid #ccc;
	padding: 3px;
	-webkit-appearance: none;
	border-radius: 0;
}

input[type="text"]:focus,
input[type="email"]:focus,
input[type="url"]:focus,
input[type="password"]:focus,
input[type="search"]:focus,
input[type="number"]:focus,
input[type="tel"]:focus,
input[type="range"]:focus,
input[type="date"]:focus,
input[type="month"]:focus,
input[type="week"]:focus,
input[type="time"]:focus,
input[type="datetime"]:focus,
input[type="datetime-local"]:focus,
input[type="color"]:focus,
textarea:focus {
	color: #111;
}

select {
	border: 1px solid #ccc;
}

textarea {
	width: 100%;
}

/*--------------------------------------------------------------
# Navigation
--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Links
--------------------------------------------------------------*/
a {
	color: royalblue;
}

a:visited {
	color: purple;
}

a:hover, a:focus, a:active {
	color: #2E7993;
}

a:focus {
	outline: thin dotted;
}

a:hover, a:active {
	outline: 0;
}

/*--------------------------------------------------------------
## Primary Navigation
--------------------------------------------------------------*/
.main-navigation .primary-menu-container {
	display: none;
}

@media screen and (min-width: 767px) {
	.main-navigation {
		display: block;
		width: 100%;
		background-color: #d91920;
		font-family: "Source Sans Pro", "Lucida Grande", "Lucida Sans Unicode", "Lucida Sans", Geneva, Arial, sans-serif;
	}
	.main-navigation .primary-menu-container {
		display: block;
		width: 100%;
		max-width: 1024px;
		margin: 0 auto;
	}
	.main-navigation .primary-menu-container ul li {
		position: relative;
		display: inline-block;
	}
	.main-navigation .primary-menu-container ul li a {
		display: inline-block;
		padding: 1em 1.25em;
		text-decoration: none;
		text-align: center;
		color: #fff;
		font-weight: 700;
	}
	.main-navigation .primary-menu-container ul li .dropdown-toggle {
		border: 1px solid #fff;
		background-color: #d91920;
		color: #fff;
		padding: 0.5em;
		margin-left: -0.5em;
		margin-right: 1.5em;
	}
	.main-navigation .primary-menu-container ul li .dropdown-toggle:before {
		font-family: "FontAwesome";
		content: '\f078';
		font-size: 1.5em;
	}
	.main-navigation .primary-menu-container ul li .dropdown-toggle.toggled {
		color: #d91920;
		background-color: #fff;
	}
	.main-navigation .primary-menu-container ul li .dropdown-toggle.toggled:before {
		content: '\f077';
	}
	.main-navigation .primary-menu-container ul ul {
		position: absolute;
		left: -9999px;
		background-color: #d91920;
		z-index: 99;
	}
	.main-navigation .primary-menu-container ul ul li {
		float: none;
		display: block;
		position: relative;
	}
	.main-navigation .primary-menu-container ul ul li a {
		text-align: left;
		white-space: nowrap;
		margin: 0;
		padding: 0.8em;
		padding-right: 3em;
	}
	.main-navigation .primary-menu-container ul ul li .dropdown-toggle {
		position: absolute;
		top: 0.5em;
		right: 0.5em;
		margin: 0;
	}
	.main-navigation .primary-menu-container ul ul li .dropdown-toggle:before {
		content: '\f054';
	}
	.main-navigation .primary-menu-container ul ul li .dropdown-toggle.toggled:before {
		content: '\f053';
	}
	.main-navigation .primary-menu-container ul ul.toggled {
		left: 0;
		margin-left: 0;
		margin-top: 0.25em;
	}
	.main-navigation .primary-menu-container ul ul ul.toggled {
		top: 0;
		left: 102%;
	}
}

.site-main .comment-navigation, .site-main
.posts-navigation, .site-main
.post-navigation {
	margin: 0 0 1.5em;
	overflow: hidden;
}

.comment-navigation .nav-previous,
.posts-navigation .nav-previous,
.post-navigation .nav-previous {
	float: left;
	width: 50%;
}

.comment-navigation .nav-next,
.posts-navigation .nav-next,
.post-navigation .nav-next {
	float: right;
	text-align: right;
	width: 50%;
}

/*--------------------------------------------------------------
## Secondary Navigation
--------------------------------------------------------------*/
.secondary-navigation {
	background-color: #000;
}

.secondary-navigation .secondary-menu-container {
	max-width: 1024px;
	margin: 0 auto;
}

.secondary-navigation .secondary-menu-container ul {
	float: right;
}

.secondary-navigation .secondary-menu-container li {
	display: inline-block;
}

.secondary-navigation .secondary-menu-container a {
	text-decoration: none;
	color: #fff;
	padding: 0.5em 0;
	font-size: 0.75em;
	margin-right: 0.5em;
}

.secondary-navigation .secondary-menu-container a:hover, .secondary-navigation .secondary-menu-container a:visited {
	color: #fff;
}

@media screen and (min-width: 567px) {
	.secondary-navigation .secondary-menu-container a {
		font-size: 1em;
		margin-right: 2em;
	}
}

/*--------------------------------------------------------------
## Mobile Navigation
--------------------------------------------------------------*/
html.fixed, body.fixed {
	height: 100%;
	overflow: hidden;
}

.main-navigation.toggled .mobile-menu-container {
	transform: translateX(0);
}

.main-navigation.toggled .mobile-nav-overlay {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(0, 0, 0, 0.7);
	z-index: 99;
}

.main-navigation .mobile-menu-container {
	position: fixed;
	top: 0;
	left: 0;
	width: 90%;
	max-width: 420px;
	height: 100%;
	transform: translateX(-100%);
	background-color: #d91920;
	transition: 0.6s ease;
	z-index: 99;
	overflow-y: scroll;
}

.main-navigation .mobile-menu-container .menu-title {
	display: block;
	padding: 0.5em 1em;
	font-size: 1.25em;
	color: #fff;
	font-weight: 700;
}

.main-navigation .mobile-menu-container ul {
	margin-left: 1em;
}

.main-navigation .mobile-menu-container ul li {
	border-top: 2px solid #eaeaea;
	border-top: 2px solid rgba(51, 51, 51, 0.1);
	position: relative;
}

.main-navigation .mobile-menu-container ul a {
	text-decoration: none;
	color: #fff;
	padding: 0.8em 0;
	display: block;
}

.main-navigation .mobile-menu-container ul ul {
	display: none;
}

.main-navigation .mobile-menu-container ul ul.toggled {
	display: block;
}

.main-navigation .mobile-menu-container ul .dropdown-toggle {
	position: absolute;
	top: 0.5em;
	right: 0.5em;
	border: 1px solid #fff;
	background-color: #d91920;
	color: #fff;
	padding: 0.5em;
}

.main-navigation .mobile-menu-container ul .dropdown-toggle:before {
	font-family: "FontAwesome";
	content: '\f078';
	font-size: 1.5em;
}

.main-navigation .mobile-menu-container ul .dropdown-toggle.toggled {
	color: #d91920;
	background-color: #fff;
}

.main-navigation .mobile-menu-container ul .dropdown-toggle.toggled:before {
	content: '\f077';
}

.main-navigation .menu-toggle {
	position: absolute;
	top: 0.5em;
	right: 0.5em;
	border: 2px solid #000;
	border-radius: 0;
	color: #000;
	background-color: transparent;
	font-size: 1em;
	z-index: 999;
	padding: 0.5em;
	font-family: "Source Sans Pro", "Lucida Grande", "Lucida Sans Unicode", "Lucida Sans", Geneva, Arial, sans-serif;
}

.main-navigation .menu-toggle:before {
	font-family: "FontAwesome";
	text-transform: lowercase;
	content: '\f0c9';
	padding-right: 0.25em;
}

.main-navigation .menu-toggle.toggled {
	color: #fff;
	background-color: #000;
}

.main-navigation .menu-toggle.toggled:before {
	text-transform: lowercase;
	content: '\f00d';
}

@media screen and (min-width: 767px) {
	.main-navigation .menu-toggle {
		display: none;
	}
}

/*--------------------------------------------------------------
# Accessibility
--------------------------------------------------------------*/
/* Text meant only for screen readers. */
.screen-reader-text {
	clip: rect(1px, 1px, 1px, 1px);
	position: absolute !important;
	height: 1px;
	width: 1px;
	overflow: hidden;
}

.screen-reader-text:focus {
	background-color: #f1f1f1;
	border-radius: 3px;
	box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
	clip: auto !important;
	color: #21759b;
	display: block;
	font-size: 14px;
	font-size: 0.875rem;
	font-weight: bold;
	height: auto;
	left: 5px;
	line-height: normal;
	padding: 15px 23px 14px;
	text-decoration: none;
	top: 5px;
	width: auto;
	z-index: 100000;
	/* Above WP toolbar. */
}

/* Do not show the outline on the skip link target. */
#content[tabindex="-1"]:focus {
	outline: 0;
}

/*--------------------------------------------------------------
# Alignments
--------------------------------------------------------------*/
.alignleft {
	display: inline;
	float: left;
	margin-right: 1.5em;
}

.alignright {
	display: inline;
	float: right;
	margin-left: 1.5em;
}

.aligncenter {
	clear: both;
	display: block;
	margin-left: auto;
	margin-right: auto;
}

/*--------------------------------------------------------------
# Clearings
--------------------------------------------------------------*/
.clear:before,
.clear:after,
.entry-content:before,
.entry-content:after,
.comment-content:before,
.comment-content:after,
.site-header:before,
.site-header:after,
.site-content:before,
.site-content:after,
.site-footer:before,
.site-footer:after {
	content: "";
	display: table;
	table-layout: fixed;
}

.clear:after,
.entry-content:after,
.comment-content:after,
.site-header:after,
.site-content:after,
.site-footer:after {
	clear: both;
}

/*--------------------------------------------------------------
# Content
--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Global
--------------------------------------------------------------*/
*, *:before, *:after {
	-moz-box-sizing: border-box;
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
}

.site-content {
	max-width: 1024px;
	margin-left: auto;
	margin-right: auto;
	padding: 1em;
}

.site-content:after {
	content: " ";
	display: block;
	clear: both;
}

.site-content #primary.full-width-content {
	width: 100%;
	float: right;
	margin-right: 0;
}

@media screen and (min-width: 767px) {
	.site-content #primary {
		width: 66.10169%;
		float: right;
		margin-right: 0;
	}
}

.site-content #primary .entry-content {
	max-width: 1024px;
	margin-left: auto;
	margin-right: auto;
}

.site-content #primary .entry-content:after {
	content: " ";
	display: block;
	clear: both;
}

.site-content #primary .entry-content .contact_info {
	border-bottom: 1px solid #d1d1d1;
}

@media screen and (min-width: 767px) {
	.site-content #primary .entry-content .contact_info {
		width: 49.15254%;
		float: left;
		margin-right: 1.69492%;
		border-bottom: none;
	}
}

.site-content #primary .entry-content .contact_info section {
	margin-bottom: 2em;
}

@media screen and (min-width: 767px) {
	.site-content #primary .entry-content .wpcf7 {
		width: 49.15254%;
		float: right;
		margin-right: 0;
	}
}

.site-content #primary .entry-content .wpcf7 input:not([type='submit']) {
	max-width: 100%;
}

.site-content #primary .entry-content .wpcf7 .wpcf7-submit {
	display: block;
	width: 100%;
	color: #fff;
	background-color: #d91920;
	font-size: 1.25em;
	padding: 0.5em 1em;
}

@media screen and (min-width: 567px) {
	.site-content #primary .entry-content .wpcf7 .wpcf7-submit {
		float: right;
		width: auto;
	}
}

@media screen and (min-width: 767px) {
	.site-content #secondary {
		width: 32.20339%;
		float: left;
		margin-right: 1.69492%;
	}
}

/*--------------------------------------------------------------
## Header
--------------------------------------------------------------*/
.site-header {
	position: relative;
	color: #000;
	background-color: #d1d1d1;
	width: 100%;
}

.site-header .header-column {
	max-width: 1024px;
	margin: 0 auto;
}

/*--------------------------------------------------------------
## Site Branding
--------------------------------------------------------------*/
.site-branding {
	width: 30%;
	min-width: 150px;
	max-width: 320px;
	height: auto;
	margin: 0.5em;
	float: left;
}

.site-branding img {
	width: 100%;
}

/*--------------------------------------------------------------
## Product Search
--------------------------------------------------------------*/
.site-search {
	float: right;
	width: 100%;
}

.site-search .widget_product_search {
	margin: 0;
}

.site-search .widget_product_search form {
	margin: 0 0.5em 0.5em;
	position: relative;
}

.site-search .widget_product_search form input[type="search"] {
	width: 100%;
	padding: 0.5em;
}

.site-search .widget_product_search form input[type="submit"],
.site-search .widget_product_search form .search_icon {
	position: absolute;
	top: 0;
	right: 0;
	color: #000;
	background-color: transparent;
	height: 100%;
	border: none;
}

.site-search .widget_product_search form input[type="submit"] {
	z-index: 9;
}

.site-search .widget_product_search form .search_icon {
	z-index: 8;
	font-size: 1.25em;
	padding: 0.25em;
}

.site-search .widget_product_search form .search_icon:before {
	font-family: "FontAwesome";
	content: '\f002';
	padding: 0 0.5em;
}

@media screen and (min-width: 767px) {
	.site-search {
		max-width: 360px;
		float: right;
	}
	.site-search .widget_product_search form {
		margin: 1em;
	}
}

/*--------------------------------------------------------------
## Content Header
--------------------------------------------------------------*/
.content-header .woocommerce-breadcrumb {
	padding-bottom: 1em;
	margin-bottom: 1em;
	font-family: "Source Sans Pro", "Lucida Grande", "Lucida Sans Unicode", "Lucida Sans", Geneva, Arial, sans-serif;
	font-size: 0.75em;
	font-weight: 700;
	border-bottom: 1px solid #d1d1d1;
}

@media screen and (min-width: 767px) {
	.content-header .woocommerce-breadcrumb {
		font-size: 1em;
	}
}

.content-header .woocommerce-breadcrumb a {
	color: #000;
	text-decoration: none;
}

.content-header .woocommerce-breadcrumb a:visited {
	color: #000;
}

.content-header .woocommerce-breadcrumb a:after {
	margin: 0 0.5em;
	font-family: "FontAwesome";
